import React, { useState, useEffect } from 'react';
import './scroll-to-top-button.css';

const ScrollToTopButton = () => {
    const [isVisible, setIsVisible] = useState(false);
    const [isAtBottom, setIsAtBottom] = useState(false);

    const handleScroll = () => {
        const scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        setIsVisible(scrollTop > 200);

        const scrollBottom = document.documentElement.scrollHeight - window.innerHeight - scrollTop;
        setIsAtBottom(scrollBottom <= 200);
    };

    const scrollToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth',
        });
    };

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        handleScroll();
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return (
        <button
            className={`scroll-to-top up-button ${isVisible ? 'opacity-100' : 'opacity-0'} right-[1rem] hidden md:bottom-[1rem] md:fixed md:block`}
            onClick={scrollToTop}
        >
            <i className={`fa-regular fa-circle-up ${isAtBottom ? "text-white" : "text-primary-dark"}`}></i>
        </button >
    );
};

export default ScrollToTopButton;

