import { useEffect } from 'react';

const UsePageMetaData = ({
    title,
    description,
    keywords,
    ogTitle,
    ogDescription,
    ogImage,
    ogUrl,
    language = 'en',
}) => {
    useEffect(() => {
        document.title = title;

        const updateMetaTag = (name, content) => {
            let tag = document.querySelector(`meta[name="${name}"]`) || document.createElement('meta');
            tag.name = name;
            tag.content = content || '';
            document.head.appendChild(tag);
        };

        const updateMetaProperty = (property, content) => {
            let tag = document.querySelector(`meta[property="${property}"]`) || document.createElement('meta');
            tag.setAttribute('property', property);
            tag.content = content || '';
            document.head.appendChild(tag);
        };

        // Set meta tags
        updateMetaTag('description', description);
        updateMetaTag('keywords', keywords);
        updateMetaTag('language', language);

        // Set Open Graph tags
        updateMetaProperty('og:title', ogTitle || title);
        updateMetaProperty('og:description', ogDescription || description);
        updateMetaProperty('og:image', ogImage);
        updateMetaProperty('og:url', ogUrl || window.location.href);
        updateMetaProperty('og:type', 'website');

    }, [title, description, keywords, ogTitle, ogDescription, ogImage, ogUrl, language]);
};

export default UsePageMetaData;