import React, { useState } from 'react'
import axios from 'axios'
import { useForm } from 'react-hook-form';
import { Helmet } from 'react-helmet-async';
// import UsePageMetaData from '../meta/UsePageMetaData';
const Contact = () => {
    // UsePageMetaData({
    //     title: 'Segen Consulting | Contact Us',
    //     description: "Chat to our friendly team!, Partner with Segen Consulting today and experience the difference in healthcare excellence.Let us be your ally in achieving exceptional patient care, regulatory compliance, and financial success.Reach out to us now to learn more about how we can support your organization's goals!",
    //     keywords: 'segen, clinical, consulting, healthcare, services, non-clinical',
    //     ogTitle: 'Segen Consulting',
    //     ogDescription: "Chat to our friendly team!, Partner with Segen Consulting today and experience the difference in healthcare excellence.Let us be your ally in achieving exceptional patient care, regulatory compliance, and financial success.Reach out to us now to learn more about how we can support your organization's goals!",
    //     ogImage: '../assets/preview-image/contact-preview.png',
    //     ogUrl: 'https://segenhealthsolutions.com/contact',
    // });
    const { register, handleSubmit, formState: { errors } } = useForm();
    const [isLoading, setIsLoading] = useState(false);
    const [statusEmail, setStatusEmail] = useState(false);
    const [emailHeader, setEmailHeader] = useState('');
    const [errorEmail, setErrorEmail] = useState(false);
    const onSubmit = async (data) => {
        setIsLoading(true);
        document.body.style.cursor = "wait";

        try {
            const response = await axios.post('/contact/send-email', data);
            setEmailHeader(response.data);
            setErrorEmail(false);
        } catch (error) {
            setEmailHeader(error.response?.data || "Something went wrong with the server. Please try again later.");
            setErrorEmail(true);
        } finally {
            setIsLoading(false);
            setStatusEmail(true);
            document.body.style.cursor = "default";
        }
    };
    return (
        <>
            <Helmet prioritizeSeoTags>
                <title>Segen Consulting | Contact</title>
                <meta name="description" content="Chat to our friendly team!, Partner with Segen Consulting today and experience the difference in healthcare excellence.Let us be your ally in achieving exceptional patient care, regulatory compliance, and financial success.Reach out to us now to learn more about how we can support your organization's goals!" />
                <meta name="keywords" content="segen, clinical, consulting, healthcare, services, non-clinical" />
                <meta property="og:title" content="Segen Consulting" />
                <meta property="og:site_name" content="Segen Consulting" />
                <meta property="og:description" content="Chat to our friendly team!, Partner with Segen Consulting today and experience the difference in healthcare excellence.Let us be your ally in achieving exceptional patient care, regulatory compliance, and financial success.Reach out to us now to learn more about how we can support your organization's goals!" />
                <meta property="og:image" content="%PUBLIC_URL%/contact-preview.png" />
                <meta property="og:url" content="https://segenhealthsolutions.com/contact" />
                <meta property="og:type" content="website" />
                <meta name="twitter:card" content="summary_large_image" />
                <meta name="twitter:title" content="Segen Consulting" />
                <meta name="twitter:description" content="Chat to our friendly team!, Partner with Segen Consulting today and experience the difference in healthcare excellence.Let us be your ally in achieving exceptional patient care, regulatory compliance, and financial success.Reach out to us now to learn more about how we can support your organization's goals!" />
                <meta name="twitter:image" content="%PUBLIC_URL%/contact-preview.png" />
                <meta name="robots" content="index, follow" />
                <meta property="og:locale" content="en_US" />
                <link rel="canonical" href="https://segenhealthsolutions.com/contact" />
            </Helmet>
            <section className='max-w-[100rem] py-[6%] mx-auto px-4 sm:px-6 lg:px-8'>
                <div>
                    <h1 className="text-[26px] lg:max-w-[70%] leading-none sm:text-[45px] md:text-[50px] lg:text-[60px] xl:text-[70px] font-bold text-primary-dark">Contact us</h1>

                    <h2 className="text-[20px] mt-4 sm:text-[30px] md:text-[35px] lg:text-[40px] xl:text-[45px] font-bold text-neutral-black leading-[25px] sm:leading-[35px] md:leading-[40px] lg:leading-[50px] xl:leading[50px]">Chat to our friendly team</h2>

                    <p className="mt-3 text-neutral-gray lg:max-w-[40%]">Partner with Segen Consulting today and experience the difference in healthcare excellence. Let us be your ally in achieving exceptional patient care, regulatory compliance, and financial success. Reach out to us now to learn more about how we can support your organization's goals!</p>
                </div>

                <div className="grid grid-cols-1 gap-12 mt-10 lg:grid-cols-2">
                    <div className="grid grid-cols-1 gap-12 md:grid-cols-2">
                        <div>
                            <span className="inline-block text-primary-dark">
                                <i className="fa-solid fa-envelope text-[30px]"></i>
                            </span>

                            <h2 className="mt-4 text-base font-bold">Email</h2>
                            <p className="mt-2 text-sm text-neutral-gray">Our friendly team is here to help.</p>
                            <p className="mt-2 text-sm text-neutral-gray">hr@segenhealthsolutions.com</p>
                        </div>

                        <div>
                            <span className="inline-block text-primary-dark">
                                <i className="fa-solid fa-comment-dots text-[30px]"></i>
                            </span>

                            <h2 className="mt-4 text-base font-bold">Live chat</h2>
                            <p className="mt-2 text-sm text-neutral-gray">Our friendly team is here to help.</p>
                            <p className="mt-2 text-sm text-neutral-gray">Start new chat</p>
                        </div>

                        {/* <div>
                        <span className="inline-block text-primary-dark">
                            <i className="fa-solid fa-location-dot text-[30px]"></i>
                        </span>

                        <h2 className="mt-4 text-base font-bold">Office</h2>
                        <p className="mt-2 text-sm text-neutral-gray">Come say hello at our office HQ.</p>
                        <p className="mt-2 text-sm text-neutral-gray">70 Waldo Avenue East rockaway NY 11518</p>
                    </div> */}

                        <div>
                            <span className="inline-block text-primary-dark">
                                <i className="fa-solid fa-phone text-[30px]"></i>
                            </span>

                            <h2 className="mt-4 text-base font-bold">Phone</h2>
                            <p className="mt-2 text-sm text-neutral-gray">Mon-Fri from 8am to 5pm.</p>
                            <p className="mt-2 text-sm text-neutral-gray">516-201-9371</p>
                        </div>
                    </div>

                    <form onSubmit={handleSubmit(onSubmit)} className="rounded-lg bg-secondary-white p-4 lg:p-8 shadow-lg">

                        <div className="-mx-2 md:items-center md:flex">
                            <div className="flex-1 px-2">
                                <label className="block mb-2 text-sm font-semibold">First Name</label>
                                <input  {...register("firstName", { required: "First name is required" })} type="text" placeholder="First Name" className="block w-full px-5 py-2.5 mt-2 placeholder-gray-400 bg-white border border-neutral-gray-light rounded-lg dark:placeholder-gray-600   focus:ring-primary-light focus:outline-none focus:ring" />
                                {errors.firstName && <p className="text-red font-semibold text-xs mt-2">{errors.firstName.message}</p>}
                            </div>

                            <div className="flex-1 px-2 mt-4 md:mt-0">
                                <label className="block mb-2 text-sm font-semibold">Last Name</label>
                                <input {...register("lastName", { required: "Last name is required" })} type="text" placeholder="Last Name" className="block w-full px-5 py-2.5 mt-2 placeholder-gray-400 bg-white border border-neutral-gray-light rounded-lg dark:placeholder-gray-600   focus:ring-primary-light focus:outline-none focus:ring" />
                                {errors.lastName && <p className="text-red font-semibold text-xs mt-2">{errors.lastName.message}</p>}
                            </div>
                        </div>
                        <div className="-mx-2 mt-2 md:items-center md:flex">
                            <div className="flex-1 px-2 mt-4 md:mt-0">
                                <label className="block mb-2 text-sm font-semibold">Mobile Number</label>
                                <input {...register("mobileNumber", {
                                    required: "Mobile number is required",
                                    pattern: {
                                        value: /^[0-9]+$/,
                                        message: "Mobile Number must be numeric only"
                                    }
                                })} type="tel" placeholder="+63 2 123 4567" className="block w-full px-5 py-2.5 mt-2 placeholder-gray-400 bg-white border border-neutral-gray-light rounded-lg dark:placeholder-gray-600   focus:ring-primary-light focus:outline-none focus:ring" />
                                {errors.mobileNumber && <p className="text-red font-semibold text-xs mt-2">{errors.mobileNumber.message}</p>}
                            </div>
                            <div className="flex-1 px-2">
                                <label className="block mb-2 text-sm font-semibold">Email</label>
                                <input {...register("email", {
                                    required: "Email is required",
                                    pattern: {
                                        value: /^[\w-.]+@([\w-]+\.)+[\w-]{2,4}$/,
                                        message: "Invalid email address"
                                    }
                                })} type="email" placeholder="email@example.com" className="block w-full px-5 py-2.5 mt-2 placeholder-gray-400 bg-white border border-neutral-gray-light rounded-lg dark:placeholder-gray-600   focus:ring-primary-light focus:outline-none focus:ring" />
                                {errors.email && <p className="text-red font-semibold text-xs mt-2">{errors.email.message}</p>}
                            </div>
                        </div>
                        <div className="mt-4">
                            <label className="block mb-2 text-sm font-semibold">Subject</label>
                            <input {...register("subject", { required: "Subject is required" })} type="text" placeholder="Subject" className="block w-full px-5 py-2.5 mt-2 placeholder-gray-400 bg-white border border-neutral-gray-light rounded-lg dark:placeholder-gray-600   focus:ring-primary-light focus:outline-none focus:ring" />
                            {errors.subject && <p className="text-red font-semibold text-xs mt-2">{errors.subject.message}</p>}
                        </div>
                        <div className="w-full mt-4">
                            <label className="block mb-2 text-sm font-semibold">Message</label>
                            <textarea  {...register("message", { required: "Message is required" })} className="block w-full h-32 px-5 py-2.5 mt-2 placeholder-gray-400 bg-white border border-neutral-gray-light rounded-lg md:h-56 dark:placeholder-gray-600   focus:ring-primary-light focus:outline-none focus:ring" placeholder="Your Message..."></textarea>
                            {errors.message && <p className="text-red font-semibold text-xs mt-2">{errors.message.message}</p>}
                        </div>
                        <button disabled={isLoading} onClick={handleSubmit} className={`${isLoading && "opacity-50"} w-full px-6 py-3 mt-4 text-sm font-medium tracking-wide text-white capitalize transition-colors duration-300 transform bg-primary-dark rounded-full`}>
                            {isLoading ? (
                                <>
                                    <svg aria-hidden="true" role="status" className="inline w-4 h-4 me-3 text-white animate-spin" viewBox="0 0 100 101" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z" fill="#E5E7EB" />
                                        <path d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z" fill="currentColor" />
                                    </svg>
                                    Processing...
                                </>
                            ) : "Send Message"}
                        </button>
                        {statusEmail && (
                            <div className={`mt-4 text-sm ${errorEmail ? 'text-red font-semibold' : 'text-green'}`}>
                                {emailHeader}
                            </div>
                        )}

                    </form>
                </div>
            </section>
        </>
    )
}

export default Contact