import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useCookies } from "react-cookie";
const LogoutButton = () => {
    const location = useNavigate();
    const [, removeCookie] = useCookies([]);
    const handleLogout = () => {
        try {
            localStorage.clear();
            removeCookie("token");
            location('/');
            window.location.reload();
        } catch (error) {
            console.error('Error logging out:', error);
        }
    };
    return (
        <button className='bg-primary-dark text-white hover:text-black mt-2 rounded-full py-2 hover:bg-primary-light transition-all' onClick={handleLogout}>
            <i className="fa-solid fa-right-from-bracket" title='Log Out'></i></button>
    );
}

export default LogoutButton;
