import React from 'react'
import "./HeroSmallMid.css"
import HeroImage from "../../assets/heroSmallDevice.jpg"
const HeroSmallMediumDevice = () => {
    return (
        <>
            <div className="relative mt-8 lg:mt-0 w-full">
                <img className="w-full md:h-[400px]  object-cover rounded-lg" src={HeroImage} alt="nurse" />
            </div>


        </>
    )
}

export default HeroSmallMediumDevice